.why-apedin {
    vertical-align: middle;
    background: #ECF0F1;
}

.why-apedin-main {
    background: transparent;
}

.why-section i {
    font-size: 40px;
}

.why-section span {
    background: #fff;
    padding: 0.85em;
    display: inline-block;
    border-radius: 50%;
    width: 65px;
    height: 65px;
}

.why-section p {
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 16px;
    font-weight: 600;
}

.why-section h4 {
    font-size: 1.5rem;
    padding: 0.5em 0.2em;
    margin-top: 25%;
    transform: translateY(-25%);
}

.why-arrow {
    margin-top: 6px;
    position: absolute;
    margin-left: 5px;
    font-size: 24px !important;
}

.drawer-icon i {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 46px;
}