.div1 {
    background: #555;
}

.cat-card {
    overflow: hidden;
    height: 140px !important;
}

.category-card {
    padding-right: 5px !important;
    padding-left: 5px !important;
    margin-bottom: 10px !important;
    cursor: pointer;
}

.card-img-overlay {
    top: 80%;
    padding: 0rem;
    background: rgba(85, 85, 85, 0.66);
    transition: 0.15s ease-in all;
}

.cat-card .card-text {
    opacity: 0;
    transition: 0.15s ease-in all;
}

.category-card:hover .card-img-overlay {
    top: 0%;
    background: rgba(85, 85, 85, 0.85);
}

.category-card:hover .card-text {
    opacity: 1;
}

